import formatTime from '../custom-methods/formatTime'

const TimerStage = ({ id, stageName, duration, styles, position }) => {
    return (
        <div id={id} className={`stage-container ${styles} ${duration === 0 ? 'passed-time' : ''}`} style={position}>
            <div className='stage-content'>
                <p className='stage-name'>{stageName}</p>
                <p className='stage-time'>{formatTime(duration, duration > 59 * 60)}</p>
            </div>
        </div>
    )
}

export default TimerStage