import TimerStage from './TimerStage'
import findAngleWithThreeSides from '../custom-methods/findAngleWithThreeSides'

const TimerCycle = ({ stagesArray, radius, stageRadius, progressBarLength, offset }) => {
    const stagesCount = stagesArray.length
    const stageDegrees = 360 / (stagesCount + 1)
    const strokeWidth = 5
    const correctionAngleDeg = findAngleWithThreeSides(stageRadius, radius, radius)
    const progressBarEndX = Math.sin((stageDegrees) * (Math.PI / 180)) * radius
    const progressBarEndY = Math.cos((stageDegrees) * (Math.PI / 180)) * radius

    const createCycle = () => {
        let cycle = []
        let stagesCounter = 1

        for (let i = 0; i < stagesCount; i++) {
            let name
            let styles
            let time = stagesArray[i]
            const degrees = (stageDegrees) * stagesCounter - correctionAngleDeg + 1  // +1 is to hide the round stroke-linecap at the end
            const radians = degrees * (Math.PI / 180)
            const position = {
                transform:
                    `translateX(calc(-50% + ${Math.sin(radians) * radius}px)) 
                    translateY(calc(-50% - ${Math.cos(radians) * radius}px))`
            }

            if (i % 2 === 0) {
                name = 'Work'
                styles = 'work-session'
            } else if (i === stagesCount - 1) {
                name = 'Long Break'
                styles = 'break-stage'
            } else {
                name = 'Short Break'
                styles = 'break-stage'
            }

            cycle.push(
                <TimerStage
                    key={stagesCounter.toString()}
                    id={stagesCounter}
                    stageName={name}
                    duration={time}
                    styles={styles}
                    position={position}
                />
            )

            stagesCounter++
        }

        return cycle
    }

    return (
        <div className='timer-cycle'>
            <svg viewBox="0 0 570 570" xmlns="http://www.w3.org/2000/svg">
                <circle
                    className="circle-passive"
                    cx="50%" cy="50%" r={radius}
                    strokeDasharray={progressBarLength}
                />
                <path
                    className="circle-active"
                    // d={`M${radius - progressBarEndX + strokeWidth},${radius - progressBarEndY + strokeWidth} A${radius},${radius} 0 1,0 ${radius + strokeWidth},${strokeWidth}`}
                    d={`
                        M${radius + strokeWidth},${strokeWidth} 
                        A${radius},${radius} 0 1,1 ${radius - progressBarEndX + strokeWidth},${radius - progressBarEndY + strokeWidth}
                    `}
                    strokeDasharray={progressBarLength}
                    strokeDashoffset={offset}
                />
            </svg>
            <div className='stages'>
                {createCycle().map((item) => item)}
            </div>
        </div>
    )
}

export default TimerCycle